import { useEffect, useState } from "react";
import AlertPopup from "./Alert";
import FullBorderedGrid from "./Grid";
import "./styles.css";

export default function App() {
  const [show, setShow] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setShow(true);
    }, 2000);
  }, []);

  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>This is My tasks, props passing, responsive grid, alert popup</h2>
      <p>For better view, open the browser in new window...</p>
      <br />
      <FullBorderedGrid />
      <br />
      {show && (
        <AlertPopup
          show={show}
          variant="filled"
          severity="success"
          setShow={setShow}
          className="custom-alert"
        >
          <div>The tasks are done successfully</div>
        </AlertPopup>
      )}
    </div>
  );
}
