import React from "react";
import Alert from "@mui/material/Alert";

const AlertPopup = ({ setShow, variant, severity, className, children }) => {
  return (
    <div className="alert-box">
      <Alert
        className={className}
        variant={variant}
        severity={severity}
        onClose={() => setShow(false)}
      >
        <div>{children}</div>
      </Alert>
    </div>
  );
};

export default AlertPopup;
